import React from "react";
import "./Services.css";
function Frontend(props) {
  return (
    <div className="frontend-container">
      <i class="medium material-icons">devices</i>
      <h5 className="service-title">Front-End Developer</h5>
      <p>
        Fast, colorful and modern web applications designed exclusively for your
        company. We use JavaScript technology such as ReactJS to provide an
        agile development to your requirements.
      </p>
      <div className="stack-icons">
        <img src="./logos/react-large.svg" alt="reactjs" />
        <img src="./logos/redux.svg" alt="redux" />
        <img src="./logos/css-3.svg" alt="css style" />
      </div>
    </div>
  );
}

export default Frontend;
