import React from "react";
import "./Portfolio.css";
import PortfolioCard from "./PortfolioCard";
function Portfolio(props) {
  const imgs = [
    {
      img: "gallery/fleet1.jpg",
      title: "Fleets app",
      description:
        "Fleets is an Android app dedicated to the transport of heavy cargo. It allows the exporters to publish their freights and the carriers to make offers to take them.",
      link: "https://github.com/ingleonelrv/fleets"
    },
    {
      img: "gallery/mern.png",
      title: "TODO App",
      description:
        "A complete web app to manage a tasks todo list, save, edit and delete. Develop con ReactJS and Materialize, using MongoDB and NodeJS as Backend.",
      link: "https://github.com/ingleonelrv/mern-task"
    },
    {
      img: "gallery/videoApp1.png",
      title: "VideoApp",
      description:
        "Android app with a custom player to watch videos from a list of favorites or recommended. Get from an API with React Native, Redux and React Navigation",
      link: "https://github.com/ingleonelrv/videoApp"
    },
    {
      img: "gallery/nyt2.png",
      title: "New York Times",
      description:
        "Small web app to search articles in the New York Times database using their api. ReactJS and Redux were used for its development.",
      link: "https://github.com/ingleonelrv/sancrisoft-nyt"
    },
    {
      img: "gallery/blog1.png",
      title: "Blog",
      description:
        "A litle blog using api jsonplaceholder, ReactJS, React Router and redux to show the information on a stylized website.",
      link: "https://github.com/ingleonelrv/blog"
    },
    {
      img: "gallery/trailerVideos.png",
      title: "videoTrailer",
      description:
        "Web application developed with ReactJS, Redux and React Router that allows you to search and watch movie trailers.",
      link: "https://github.com/ingleonelrv/trailerVideosReact"
    }
  ];
  return (
    <div id="portfolio">
      <h4>My Remarkable Work</h4>
      <div className="portfolio-container">
        <div className="row portfolio-row">
          {imgs.map(img => {
            return <PortfolioCard {...img} />;
          })}
        </div>
      </div>
    </div>
  );
}

export default Portfolio;
