import React from "react";

function Navbar(props) {
  return (
    <div className="navbar-fixed">
      <nav className="grey lighten-5 ">
        <div className="container ">
          <div className="nav-wrapper">
            <a href="#" className="left ">
              <h5 className="cyan-text text-darken-2 my-name">
                <strong>Leonel Rodriguez</strong>
              </h5>
            </a>
            <ul id="nav-mobile" className="right hide-on-med-and-down">
              <li>
                <a href="#" className="grey-text text-darken-2 ">
                  Home
                </a>
              </li>
              <li>
                <a href="#about" className="grey-text text-darken-2">
                  About
                </a>
              </li>
              <li>
                <a href="#services" className="grey-text text-darken-2">
                  Services
                </a>
              </li>
              <li>
                <a href="#portfolio" className="grey-text text-darken-2">
                  Portfolio
                </a>
              </li>
              {/* <li>
                <a href="#" className="grey-text text-darken-2">
                  Contact
                </a>
              </li> */}
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Navbar;
