import React from "react";
import "./Services.css";
function Movil(props) {
  return (
    <div className="movil-container">
      <i class="medium material-icons">developer_mode</i>
      <h5>Mobile Developer</h5>
      <p>
        If your company does not yet have a mobile application, we will build
        it. Using React Native we can provide a complete and efficient solution
        for both Android and iOS.
      </p>
      <div className="stack-icons">
        <img src="./logos/react-large.svg" alt="reactjs" />
        <img src="./logos/firebase.svg" alt="firebase" />
        <img src="./logos/android-logo.svg" alt="android" />
      </div>
    </div>
  );
}

export default Movil;
